.c-hamburger {
  position: absolute;
  right: 20px;
  top: 10px; }
  .c-hamburger .line {
    width: 40px;
    height: 5px;
    background-color: #333333;
    display: block;
    margin: 4px auto;
    -webkit-transition: all 0.3s ease-in-out;
