@-webkit-keyframes bounce {
  0% {
    height: 50px; }
  25% {
    height: 55px; }
  75% {
    height: 60px; }
  100% {
    height: 50px; } }

@keyframes bounce {
  0% {
    height: 50px; }
  25% {
    height: 55px; }
  75% {
    height: 60px; }
  100% {
    height: 50px; } }

@media (max-width: 768px) {
