  .c-hamburger {
    top: 12px;
    right: 16px; }
    .c-hamburger.active {
      top: 13px;
      right: 12px; }
      .c-hamburger.active .line {
        margin: 10px auto; }
    .c-hamburger .line {
      width: 26px;
      height: 3px; } }



