.c-map .leaflet-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  background: white; }
  .c-map .leaflet-container .leaflet-pane.leaflet-tile-pane {
    opacity: 0.75; }
  .c-map .leaflet-container .leaflet-pane .c-feature {
    pointer-events: auto;
    cursor: pointer;
    fill: #ed6000;
    stroke: #ed6000;
    stroke-width: 2px;
    stroke-opacity: 1; }
    .c-map .leaflet-container .leaflet-pane .c-feature.adjacent {
      stroke-dasharray: 10 25;
      fill-opacity: 0; }
    .c-map .leaflet-container .leaflet-pane .c-feature:hover, .c-map .leaflet-container .leaflet-pane .c-feature.active {
      fill-opacity: 0.9; }
    .c-map .leaflet-container .leaflet-pane .c-feature.adjacent:hover {
      fill-opacity: 0.4; }
  .c-map .leaflet-container .leaflet-pane .leaflet-tooltip.c-tooltip {
    margin: 0;
    padding: 10px;
    color: black;
    border: 1px #ed6000;
    -webkit-box-shadow: none;
            box-shadow: none;
    background: transparent -webkit-linear-gradient(top, transparent 0%, rgba(200, 200, 200, 0.7) 10px, rgba(200, 200, 200, 0.7) calc(100% - 10px), transparent 100%);
    background: transparent -o-linear-gradient(top, transparent 0%, rgba(200, 200, 200, 0.7) 10px, rgba(200, 200, 200, 0.7) calc(100% - 10px), transparent 100%);
    background: transparent linear-gradient(to bottom, transparent 0%, rgba(200, 200, 200, 0.7) 10px, rgba(200, 200, 200, 0.7) calc(100% - 10px), transparent 100%); }
    .c-map .leaflet-container .leaflet-pane .leaflet-tooltip.c-tooltip:before {
      content: none; }
    .c-map .leaflet-container .leaflet-pane .leaflet-tooltip.c-tooltip.leaflet-tooltip-left {
      text-align: right; }
    .c-map .leaflet-container .leaflet-pane .leaflet-tooltip.c-tooltip h1 {
      margin: 0; }
    @media (max-width: 767px) {
      .c-map .leaflet-container .leaflet-pane .leaflet-tooltip.c-tooltip h1 {
        font-size: 1.7em; } }



