.c-searchBox {
  position: relative;
  display: block;
  width: 370px;
  height: 50px;
  text-align: left; }
  .c-searchBox input {
    width: 100%;
    color: black;
    line-height: 30px;
    font-family: sans-serif;
    font-size: 16px;
    padding: 10px 40px 10px 10px;
    border: none;
    font-weight: lighter; }
    .c-searchBox input:focus {
      outline: 0;
      border: none; }
  .c-searchBox:hover input {
    outline: 0;
    border: none; }
    .c-searchBox:hover input:focus {
      outline: 0;
      border: none; }

@media (max-width: 670px) {
  .c-searchBox {
    width: 100%; }
  .filtersHeader + .c-container {
    margin: 45px auto 15px auto;
    width: 80vw; } }

@media (max-height: 360px) {
  .filtersHeader + .c-container {
    margin-top: 25px; } }



